import React, { useState, useEffect } from 'react';
import SetTimeComponent from '../SetTimeComponent/SetTimeComponent';
import ProgressComponent from '../ProgressComponent/ProgressComponent';
import { Button } from '@mui/material';
import { SButtonsWrap, SCountdownWrap } from './countdown.style';
import soundUtils from '../../utils/soundUtils';
// @ts-ignore
import soundFile from '../../assets/sound.mp3';

const defaultTime = {
    minutes: 0,
    seconds: 0,
};

function CountdownComponent() {
    const [time, setTime] = useState(defaultTime);
    const [run, setRun] = useState(false);
    const [disabled, setDisabled] = useState(false);
    const [progress, setProgress] = useState(0);
    const [timeStart, setTimeStart] = useState(0);

    const totalSeconds = time.minutes * 60 + time.seconds;

    useEffect(() => {
        let timer: any;

        if (run) {
            timer = setInterval(() => {
                setTime(prevTime => {
                    let newMinutes = prevTime.minutes;
                    let newSeconds = prevTime.seconds - 1;

                    if (newSeconds < 0) {
                        newMinutes -= 1;
                        newSeconds = 59;
                    }

                    if (newMinutes < 0) {
                        newMinutes = 0;
                        newSeconds = 0;
                        setRun(false);
                        setDisabled(false);
                        clearInterval(timer);
                        setProgress(0);
                        soundUtils(soundFile);
                    }
                    return {
                        minutes: newMinutes,
                        seconds: newSeconds,
                    };
                });
            }, 1000);
        }

        return () => clearInterval(timer);
    }, [run, time]);

    useEffect(() => {
        if (!disabled) {
            setTimeStart(totalSeconds);
        }
    }, [disabled, totalSeconds]);

    function isRun() {
        setRun(run => !run);
        setDisabled(true);
    }

    function handleReset() {
        setRun(false);
        setDisabled(false);
        setTime(defaultTime);
        setProgress(0);
        setTimeStart(0);
    }

    return (
        <SCountdownWrap>
            <h1>
                {time.minutes.toString().padStart(2, '0')} : {time.seconds.toString().padStart(2, '0')}
            </h1>
            <SetTimeComponent time={time} isSetTime={setTime} disabled={disabled} />
            <SButtonsWrap>
                <Button variant='contained' onClick={isRun}>
                    {run ? '' : ''}
                </Button>
                <Button variant='outlined' onClick={handleReset}>
                    
                </Button>
            </SButtonsWrap>
            <ProgressComponent
                time={time}
                run={run}
                progress={progress}
                isSetProgress={setProgress}
                timeStart={timeStart}
            />
        </SCountdownWrap>
    );
}

export default React.memo(CountdownComponent);
