import React, { useEffect } from 'react';
import LinearProgress from '@mui/material/LinearProgress';
import { SProgressWrap } from './progress.style';

interface ProgressComponentProps {
    time: {
        minutes: number;
        seconds: number;
    };
    run: boolean;
    progress: number;
    timeStart: number;
    isSetProgress: React.Dispatch<React.SetStateAction<number>>;
}

function ProgressComponent({ time, run, progress, isSetProgress, timeStart }: ProgressComponentProps) {
    const totalSeconds = time.minutes * 60 + time.seconds;

    useEffect(() => {
        let interval: any;

        if (run && totalSeconds > 0) {
            interval = setTimeout(() => {
                const calculatedProgress = Math.abs(run ? (100 * totalSeconds) / timeStart - 100 : 0);
                isSetProgress(calculatedProgress);

                if (totalSeconds === 0) {
                    clearTimeout(interval);
                }
            }, 1000);
        }
        return () => clearTimeout(interval);
    }, [run, totalSeconds, isSetProgress, timeStart]);

    return (
        <SProgressWrap>
            <LinearProgress variant='determinate' value={progress} />
            {progress.toFixed(0)}%
        </SProgressWrap>
    );
}

export default React.memo(ProgressComponent);
