import React, { useEffect, useState } from 'react';
import { Slider, TextField } from '@mui/material';
import { SSetTimeMin, SSetTimeSec, SSetTimeWrap } from './settime.style';

interface SetTimeProps {
    time: {
        minutes: number;
        seconds: number;
    };
    isSetTime: (newTime: { minutes: number; seconds: number }) => void;
    disabled: boolean;
}

function SetTimeComponent({ time, isSetTime, disabled }: SetTimeProps) {
    const [inputMinutes, setInputMinutes] = useState('');
    const [inputSeconds, setInputSeconds] = useState('');
    const [sliderValue, setSliderValue] = useState(0);

    useEffect(() => {
        if (disabled) {
            setInputMinutes('');
            setInputSeconds('');
            setSliderValue(0);
        } else {
            setInputMinutes(time.minutes.toString());
            setInputSeconds(time.seconds.toString());
            setSliderValue(time.minutes * 60 + time.seconds);
        }
    }, [disabled, time.minutes, time.seconds]);

    const handleMinutesChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let newMinutes = parseInt(event.target.value);
        if (isNaN(newMinutes) || newMinutes < 0 || newMinutes > 720) {
            newMinutes = 0; //   0,    
        }
        const newTime = {
            ...time,
            minutes: newMinutes,
        };
        isSetTime(newTime);
        setInputMinutes(newMinutes.toString());
        setSliderValue(newMinutes * 60 + time.seconds);
    };

    const handleSecondsChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let newSeconds = parseInt(event.target.value);
        if (isNaN(newSeconds) || newSeconds < 0 || newSeconds >= 60) {
            newSeconds = 0; //   0,    
        }
        const newTime = {
            ...time,
            seconds: newSeconds,
        };
        isSetTime(newTime);
        setInputSeconds(newSeconds.toString());
        setSliderValue(time.minutes * 60 + newSeconds);
    };

    const handleSliderChange = (event: Event, newValue: number | number[]) => {
        if (typeof newValue === 'number') {
            const newMinutes = Math.floor(newValue / 60);
            const newSeconds = newValue % 60;
            const newTime = {
                ...time,
                minutes: newMinutes,
                seconds: newSeconds,
            };
            isSetTime(newTime);
            setInputMinutes(newMinutes.toString());
            setInputSeconds(newSeconds.toString());
            setSliderValue(newValue);
        }
    };

    return (
        <SSetTimeWrap>
            <SSetTimeMin>
                <TextField
                    id='outlined-basic'
                    label=''
                    variant='outlined'
                    size='small'
                    value={inputMinutes}
                    onChange={handleMinutesChange}
                    disabled={disabled}
                    autoComplete='off'
                />
            </SSetTimeMin>
            <SSetTimeSec>
                <TextField
                    id='outlined-basic'
                    label=''
                    variant='outlined'
                    size='small'
                    value={inputSeconds}
                    onChange={handleSecondsChange}
                    disabled={disabled}
                    autoComplete='off'
                />
            </SSetTimeSec>
            <Slider
                aria-label='Temperature'
                value={sliderValue}
                valueLabelDisplay='auto'
                step={15}
                marks
                min={0}
                max={60 * 60} // 60 minutes * 60 seconds
                onChange={handleSliderChange}
                disabled={disabled}
            />
        </SSetTimeWrap>
    );
}

export default React.memo(SetTimeComponent);
